import { NativeModules } from 'react-native';
import initialProps from "../../Initial";
import {newError} from "../../utils/Utils";

const robotMapManager = NativeModules.MapManager;

export class RobotMapManager {

    /**
     * 通过t9搜索地图点位
     * @param query
     */
    public static t9Search(query: string): string {
        if (initialProps.mapControlVer > 0) {
            return robotMapManager.t9Search(query);
        } else {
            let msg = 'The device does not support t9Search API.';
            newError(msg);
            return '';
        }
    }
}
