import { NativeModules } from 'react-native';
import * as _ from 'lodash';
import { newError } from '../../utils/Utils';
import initialProps from '../../Initial';

const messagePushApi = NativeModules.MsgPushManager;

export enum MsgPushType {
    APP_OBTAIN = 1,
    BOOT_RECOMMEND,
    OTA_RECOMMEND
}

export class MsgPushManager {

    // 获取指定类型，appId, appPath对应的消息列表（包括对应版本号）
    public static getMessages(type: number = MsgPushType.APP_OBTAIN, appId?: string, appPath?: string): string {
        if (initialProps.msgControlVer > 0) {
            return messagePushApi.getMessages(type, appId, appPath);
        } else {
            newError('Your ModuleApp do not support getMessages');
            return '';
        }
    }

    // 获取指定msgId对应的消息具体内容
    public static getMessageContent(msgId: number): string {
        if (initialProps.msgControlVer > 0) {
            return messagePushApi.getMessageContent(msgId);
        } else {
            newError('Your ModuleApp do not support getMessageContent');
            return '';
        }
    }

    // 获取指定detailId对应的消息具体内容
    public static getDetail(detailId: number): string {
        if (initialProps.msgControlVer > 1) {
            return messagePushApi.getDetail(detailId);
        } else {
            newError('Your ModuleApp do not support getDetail');
            return '';
        }
    }

    // 将指定detailId对应的消息具体内容播放次数+1
    public static increaseDetailPlayTimes(detailId: number) {
        if (initialProps.msgControlVer > 1) {
            messagePushApi.increaseDetailPlayTimes(detailId);
        } else {
            newError('Your ModuleApp do not support increaseDetailPlayTimes');
        }
    }

    // 删除指定detailId对应的消息具体内容
    public static deleteDetail(detailId: number) {
        if (initialProps.msgControlVer > 1) {
            messagePushApi.deleteDetail(detailId);
        } else {
            newError('Your ModuleApp do not support deleteDetail');
        }
    }

    // 监听指定类型，appId, appPath对应的消息列表变化
    public static registerMsgChanged(callbackId: number, type: number, appId?: string, appPath?: string): void {
        if (initialProps.msgControlVer > 0) {
            if (!_.isNumber(callbackId) || callbackId < 0) {
                newError('MsgPushListener callbackId must be validate.');
            } else {
                messagePushApi.registerMsgChanged(callbackId, type, appId, appPath);
            }
        } else {
            newError(
                'Your ModuleApp do not support monitor msg push changes.'
            );
        }
    }

    public static unregisterMsgChanged(callbackId: number): void {
        if (initialProps.msgControlVer > 0) {
            if (!_.isNumber(callbackId) || callbackId < 0) {
                newError('MsgPushListener callbackId must be validate.');
            } else {
                messagePushApi.unregisterMsgChanged(callbackId);
            }
        } else {
            newError(
                'Your ModuleApp do not support monitor msg push changes.'
            );
        }
    }


}
