import React from 'react';
import { AppManager } from '../../robot/api/AppManager';

const TAG = 'NlpPath';

/**
 * OPK的id
 * @type {string}
 */
const appId = AppManager.getAppId();

/**
 * 默认path
 *
 * 默认path情况下表示支持所有语音指令
 * @type {string}
 */
const defaultPath = 'main';

/**
 * 当前所处的path
 * @type {null}
 */
let path = null;

function setPath(pathName, action) {
    console.log(TAG, 'Set path : ' + pathName + '   ' + action + '   ' + appId);

    AppManager.setAppInfo(appId, pathName);
    if (action) {
        action.start();
    }
}

/**
 * Path显示时候的处理
 * @param pathName
 * @param action
 */
function onPathMount(pathName, action) {
    console.log(
        TAG,
        'On path mount : ' + pathName + '   ' + action + '   ' + appId
    );

    let prePath = path;
    path = {
        pathName: pathName,
        action: action,
        pre: prePath
    };

    setPath(pathName, action);
}

/**
 * Path隐藏时候的处理
 * @param pathName
 * @param action
 */
function onPathUnmount(pathName, action) {
    console.log(TAG, 'On path unmount : ' + pathName);
    if (action) {
        action.stop();
    }

    //界面切换的时候先执行的是新界面的mount，后执行老界面的unmount
    if (path.pre && path.pre.pathName === pathName) {
        path.pre = path.pre.pre;
        return;
    }

    if (path.pathName === pathName) {
        if (path.pre) {
            path = path.pre;
            setPath(path.pathName, path.action);
        } else {
            setPath(defaultPath);
        }
    }
}

/**
 * 作为NlpPath处理的子View的包装类
 */
class NlpViewPath extends React.Component {
    constructor(props) {
        super(props);
        this.pathName = this.props.pathName;
        this.action = this.props.action;
    }

    componentWillMount() {
        onPathMount(this.pathName, this.action);
    }

    componentWillUnmount() {
        onPathUnmount(this.pathName, this.action);
    }

    render() {
        return <>{this.props.children}</>;
    }
}

/**
 * NlpPath修饰在类方法或属性上的处理
 *
 * @param target 类的原型
 * @param name   类方法或属性名称
 * @param descriptor  被装饰参数的属性，descriptor.value为函数本体
 * @param pathName NlpPath名称
 * @param action 处理当前Path支持的Action的对象，生命周期为start和stop
 */
function decoratorViewPath(target, name, descriptor, pathName, action) {
    console.log('NlpPath view ********** : ' + pathName);
    const old = descriptor.value;

    descriptor.value = function(...arg) {
        let result = old.apply(this, arg);
        return React.createElement(
            NlpViewPath,
            { ...arg, pathName: pathName, action: action },
            result
        );
    };
}

/**
 * NlpPath修饰在类上的处理
 *
 * @param target 类对象本身
 * @param pathName NlpPath名称
 * @param action 处理当前Path支持的Action的对象，生命周期为start和stop
 */
function decoratorPath(target, pathName, action) {
    console.log('NlpPath class ********** : ' + pathName);
    const targetWM = target.prototype.componentWillMount;
    target.prototype.componentWillMount = function() {
        targetWM && targetWM.call(this);
        onPathMount(pathName, action);
    };

    const targetWUM = target.prototype.componentWillUnmount;
    target.prototype.componentWillUnmount = function() {
        targetWUM && targetWUM.call(this);
        onPathUnmount(pathName, action);
    };
}

/**
 * 包装可作为NlpPath的类或类的方法
 *
 * @param pathName nlp path名称
 * @param action   action处理
 * @param taskFlow 多轮对话
 */
function NlpPath(pathName, action = null, taskFlow = null) {
    return function(target, name, descriptor) {
        if (descriptor) {
            decoratorViewPath(target, name, descriptor, pathName, action);
        } else {
            decoratorPath(target, pathName, action);
        }
    };
}

export { NlpPath };
