import { NativeModules } from 'react-native';
import initialProps from '../../Initial';
import { newError } from '../../utils/Utils';

const robotSettingApi = NativeModules.RobotSettingApi;

export class RobotSettingApi {
    private static readonly appId = null;

    public static getRobotString(key: string): Promise<string> {
        if (initialProps.isSupportRobotSettingApi) {
            return robotSettingApi.getRobotString(key);
        } else {
            let msg = 'This system version does not support getRobotString.';
            newError(msg);
            return Promise.reject(msg);
        }
    }

    public static getRobotFloat(key: string): Promise<number> {
        if (initialProps.isSupportRobotSettingApi) {
            return robotSettingApi.getRobotFloat(key);
        } else {
            let msg = 'This system version does not support getRobotFloat.';
            newError(msg);
            return Promise.reject(msg);
        }
    }

    public static getRobotInt(key: string): Promise<number> {
        if (initialProps.isSupportRobotSettingApi) {
            return robotSettingApi.getRobotInt(key);
        } else {
            let msg = 'This system version does not support getRobotInt.';
            newError(msg);
            return Promise.reject(msg);
        }
    }

    public static setRobotString(key: string, value: string): void {
        if (initialProps.isSupportRobotSettingApi) {
            if (this.appId && initialProps.isSupportRobotSettingPermission) {
                robotSettingApi.setRobotString(this.appId, key, value);
            } else {
                robotSettingApi.setRobotString(key, value);
            }
        } else {
            newError('This system version does not support setRobotString.');
        }
    }

    public static setRobotFloat(key: string, value: number): void {
        if (initialProps.isSupportRobotSettingApi) {
            if (this.appId && initialProps.isSupportRobotSettingPermission) {
                robotSettingApi.setRobotFloat(this.appId, key, value);
            } else {
                robotSettingApi.setRobotFloat(key, value);
            }
        } else {
            newError('This system version does not support setRobotFloat.');
        }
    }

    public static setRobotInt(key: string, value: number): void {
        if (initialProps.isSupportRobotSettingApi) {
            if (this.appId && initialProps.isSupportRobotSettingPermission) {
                robotSettingApi.setRobotInt(this.appId, key, value);
            } else {
                robotSettingApi.setRobotInt(key, value);
            }
        } else {
            newError('This system version does not support setRobotInt.');
        }
    }

    public static registerRobotSettingListener(
        callbackId: number,
        keys: string[]
    ): void {
        if (initialProps.isSupportRobotSettingApi) {
            robotSettingApi.registerRobotSettingListener(callbackId, keys);
        } else {
            newError(
                'This system version does not support registerRobotSettingListener.'
            );
        }
    }

    public static unRegisterRobotSettingListener(callbackId: number): void {
        if (initialProps.isSupportRobotSettingApi) {
            robotSettingApi.unRegisterRobotSettingListener(callbackId);
        } else {
            newError(
                'This system version does not support unRegisterRobotSettingListener.'
            );
        }
    }

    public static isCharging(): Promise<boolean> {
        if (initialProps.robotSettingApiVersion >= 1) {
            return robotSettingApi.isCharging();
        } else {
            let msg = 'This system version does not support isCharging.';
            newError(msg);
            return Promise.reject(msg);
        }
    }

    public static clearSettingsListener(): void {
        if (initialProps.robotSettingApiVersion >= 1) {
            robotSettingApi.clearSettingsListener();
        } else {
            newError(
                'This system version does not support unRegisterRobotSettingListener.'
            );
        }
    }
}
