import * as _ from 'lodash';
import {
    NavigationCheckParam,
    NavigationTransferParam
} from './definition/ComponentParamConst';
import {
    BaseRobotComponent,
    BaseRobotProps,
    ResouceType,
    RobotComponentInfo
} from './BaseRobotComponent';
import {DEV_ERROR_SUPPORT, getuuid, newError} from '../../utils/Utils';
import initialProps from "../../Initial";

let sprintf = require('sprintf-js').sprintf;

interface NavigationCheckProps extends BaseRobotProps {
    param: NavigationCheckParam;
}

const COMPONENT_NATIVE_BRIDGE_NAME = 'NavigationCheck';

/**
 * @desc: 多机导航检测点位空闲组件
 * @author 宋奔
 * @date 2021-03-04
 */
export class NavigationCheckComponent extends BaseRobotComponent<NavigationCheckProps, NavigationCheckParam
> {
    protected constructor(props: NavigationCheckProps) {
        super(props);
        if (initialProps.multiRobotNavigationVer <= 0) {
            newError(DEV_ERROR_SUPPORT);
        }
    }

    public getComponentParam(): NavigationCheckParam | undefined {
        return this.props.param;
    }

    public generateComponentInfo(): RobotComponentInfo {
        return new RobotComponentInfo(
            COMPONENT_NATIVE_BRIDGE_NAME,
            getuuid(),
            true,
            true,
            false,
            [ResouceType.Navigation]
        );
    }
}
