import {
    DeviceEventEmitter,
    EmitterSubscription,
    NativeModules
} from 'react-native';
import initialProps from '../../Initial';
import { DEV_ERROR_SUPPORT, newError, newWarn } from '../../utils/Utils';

const appManager = NativeModules.AppManager;

/**
 * opk相关信息类
 */
export class AppManager {
    private static readonly appId = null;
    private static configUpdate: EmitterSubscription;
    private static systemConfigUpdate: EmitterSubscription;

    /**
     * 获取当前opk　信息
     * @returns opk 信息json串
     */
    public static getAppJson(): string {
        if (this.appId) {
            if (initialProps.isSupportPlugin) {
                return appManager.getAppJsonByAppId(this.appId);
            } else {
                newError(DEV_ERROR_SUPPORT);
                return '';
            }
        } else {
            try {
                return appManager.getAppJson();
            } catch (error) {
                newError(error);
                return '';
            }
        }
    }

    /**
     * 获取当前业务bundle　路径
     * @returns bundle　路径
     */
    public static getOpkPath(): string {
        if (this.appId) {
            if (initialProps.isSupportPlugin) {
                return appManager.getOpkPathByAppId(this.appId);
            } else {
                newError(DEV_ERROR_SUPPORT);
                return '';
            }
        } else {
            try {
                return appManager.getOpkPath();
            } catch (error) {
                newError(error);
                return '';
            }
        }
    }

    /**
     * 获取当前opk　额外资源路径
     * @returns 额外资源路径
     */
    public static getOpkExtraPath(): string {
        if (this.appId) {
            if (initialProps.isSupportPlugin) {
                return appManager.getOpkExtraPathByAppId(this.appId);
            } else {
                newError(DEV_ERROR_SUPPORT);
                return '';
            }
        } else {
            try {
                return appManager.getOpkExtraPath();
            } catch (error) {
                newError(error);
                return '';
            }
        }
    }

    /**
     * 获取当前加载角色的appid　支持debug
     */
    public static getAppId(): string {
        if (this.appId) {
            // v3 插件的 appid
            // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
            return this.appId!!;
        }
        if (initialProps.isSupportAppId) {
            try {
                let appJson = AppManager.getAppJson();
                if (appJson) {
                    return JSON.parse(appJson).appid;
                }
                newWarn('getAppId AppJson error');
                return '';
            } catch (error) {
                newWarn(error);
                return '';
            }
        }
        return '';
    }

    /**
     * 获取OPK图标
     */
    public static getAppIcon(): string {
        if (initialProps.isSupportPlugin) {
            return appManager.getAppIcon(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
            return '';
        }
    }

    /**
     * 获取OPK配置
     */
    public static getAppConfig(): string {
        if (initialProps.isSupportPlugin) {
            return appManager.getAppConfig(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
            return '';
        }
    }

    /**
     * 设置Opk
     */
    public static setAppConfig(key: string, value: string): void {
        if (initialProps.isSupportConfigUpdateNotify && this.appId) {
            appManager.setAppConfig(this.appId, key, value);
        } else {
            newError(DEV_ERROR_SUPPORT);
        }
    }

    /**
     * 设置App配置变化监听
     */
    public static setConfigUpdateListener(
        onConfigUpdate: (config: any) => void
    ) {
        if (initialProps.isSupportConfigUpdateNotify && this.appId) {
            if (this.configUpdate) {
                this.configUpdate.remove();
            }
            this.configUpdate = DeviceEventEmitter.addListener(
                'configUpdate_' + this.appId,
                (data: any) => {
                    let config = JSON.parse(data);
                    onConfigUpdate(config);
                }
            );
        } else {
            newError(DEV_ERROR_SUPPORT);
            return '';
        }
    }

     /**
     * 设置系统配置变化监听
     * @param onConfigUpdate 监听系统配置,接收配置数据
     *
     */
    public static setSystemConfigUpdateListener(
        onConfigUpdate: (config: any) => void
    ) {
        let systemId = this.getSystemId();
        if (initialProps.isSupportConfigUpdateNotify && systemId) {
            if (this.systemConfigUpdate) {
                this.systemConfigUpdate.remove();
            }
            this.systemConfigUpdate = DeviceEventEmitter.addListener(
                'configUpdate_' + systemId,
                (data: any) => {
                    let config = JSON.parse(data);
                    onConfigUpdate(config);
                }
            );
        } else {
            newError(DEV_ERROR_SUPPORT);
            return '';
        }
    }

    /**
     * 获取系统配置ID
     * 作用: 小程序下发的系统配置ID
    */
    private static getSystemId(): string{
        if (initialProps.isSupportSystemId) {
            return appManager.getSystemId();
        } else {
            newError(DEV_ERROR_SUPPORT);
            return '';
        }
    }

    /**
     * 重启OPK
     */
    public static restartApp() {
        if (initialProps.isSupportPlugin) {
            appManager.restartApp(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
        }
    }

    public static startApp(appId?: string) {
        if (initialProps.isSupportNlpParser) {
            appManager.startApp(appId);
        } else {
            let msg = 'The device does not support startApp API.';
            newError(msg);
        }
    }

    public static destroyApp(appId?: string) {
        if (initialProps.isSupportNlpParser) {
            appManager.destroyApp(appId);
        } else {
            let msg = 'The device does not support destroyApp API.';
            newWarn(msg);
        }
    }

    public static moveToForeground(appId?: string) {
        if (initialProps.isSupportNlpParser) {
            appManager.moveToForeground(appId);
        } else {
            let msg = 'The device does not support moveToForeground API.';
            newWarn(msg);
        }
    }

    public static moveToBack(appId?: string) {
        if (initialProps.isSupportNlpParser) {
            appManager.moveToBack(appId);
        } else {
            let msg = 'The device does not support moveToBack API.';
            newWarn(msg);
        }
    }

    public static setAppPath(appId?: string, path?: string) {
        if (initialProps.isSupportNlpParser) {
            appManager.setAppPath(appId, path);
        } else {
            let msg = 'The device does not support setAppPath API.';
            newError(msg);
        }
    }

    public static setAppVersion(appId?: string, version?: string) {
        let msg = 'The device does not support setAppVersion API.';
        newWarn(msg);
    }

    /**
     * 可变参数动态设置当前opk 的 nlp state
     * @param appId 当前opk 的 appId
     * @param path 当前界面的 path
     * @param subPage 预留三级界面
     * @param version opk 版本
     */
    public static setAppInfo(
        appId?: string,
        path?: string,
        subPage?: string,
        version?: string
    ) {
        if (initialProps.isSupportNlpParser) {
            if (appId) {
                appManager.setAsyncCustomNlpData(
                    'opt_scene_switch',
                    JSON.stringify({
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        app_id: appId,
                        path: path == undefined ? '' : path,
                        subPage: subPage == undefined ? '' : subPage,
                        version: version == undefined ? '' : version
                    })
                );
            } else {
                let msg = 'nlp_launcher_:setAppInfo appId is null';
                newError(msg);
            }
        } else {
            let msg = 'The device does not support setAsyncCustomNlpData API.';
            newError(msg);
        }
    }
}
