import { DeviceEventEmitter } from 'react-native';
import {
    NavigationScreenProp,
    NavigationState,
    NavigationParams,
    NavigationNavigateActionPayload,
    NavigationReplaceActionPayload
} from 'react-navigation';

import { Crash } from './utils/Crash';
import { speechApi } from './robot/api/NativeApi';
import { emojiPlayerModel } from './models/EmojiPlayerModel';
import { NlpScene } from './nlp/scene/NlpScene';
import { triggerManager, TriggerNavigate } from './TriggerManager';

export enum TriggerType {
    api = 1,
    voice,
    ui,
    deepLink
}

export interface TriggerProtocol {
    channel: string;
    type: TriggerType;
    eventId: number;
    result: any;
    goBack?: boolean;
    putStack?: boolean;
}

export abstract class Trigger {
    protected _channel: string;
    private readonly appId = null;

    public constructor(channel: string) {
        if (this.appId) {
            this._channel = this.appId + '_' + channel;
        } else {
            this._channel = channel;
        }
        triggerManager.addTrigger(this);
    }

    public getChannel = (): string => {
        return this._channel;
    };

    public dispatch = (data: TriggerProtocol): void => {
        data.eventId = data.eventId || -1;
        this.trigger(data);
    };

    protected goBack = (data: TriggerProtocol): void => {
        if (triggerManager.type === TriggerNavigate.stackNavigate) {
            triggerManager.getNavigation().goBack();
            return;
        }
        let stackInfo = triggerManager.popRouteName();
        if (stackInfo) {
            data.result = stackInfo.protocol && stackInfo.protocol.result;
            data.goBack = true;
            this._trigger(stackInfo.name, data);
        }
    };

    protected _trigger = (
        router: string,
        data: TriggerProtocol,
        goBack = false
    ): void => {
        let navigation = triggerManager.getNavigation();
        let current = navigation.state;
        data.channel = current.routeName;
        console.log('caller: ', this._channel, current, data);
        Crash.putCurrentScenesState({
            Name: router
        });
        //todo 切换失败的处理
        NlpScene.switchScene(router);
        /**
         State: navigation.state,
         Protocol: data*/
        if (triggerManager.middleware) {
            triggerManager.middleware(current.routeName, router, data);
        }
        if (triggerManager.type === TriggerNavigate.replaceNavigate) {
            navigation.replace(router, data);
        } else {
            navigation.navigate(router, data);
        }
        if (data.goBack !== true) {
            if (data.channel !== router) {
                let existRoute = triggerManager.isRouteExistInStack(router);
                if (existRoute) {
                    triggerManager.popTo(router);
                } else {
                    if (data.putStack !== false) {
                        triggerManager.pushRouteName(data.channel, data);
                    }
                }
            }
        }
        this.navSwitch(data.channel, router);
    };

    public stopTTS = (): void => {
        speechApi.stopTTS();
    };

    public playTextWithEmoji = (listenerId: number, text: string): void => {
        //显示大眼睛
        emojiPlayerModel.setShow(true);
        speechApi.playText(listenerId, text);
    };

    abstract trigger(protocol: TriggerProtocol): void | boolean;
    abstract navSwitch(from: string, to: string): void;
}
