import {
    DeviceEventEmitter,
    EmitterSubscription,
    NativeModules
} from 'react-native';
import initialProps from '../../Initial';
import { DEV_ERROR_SUPPORT, newError } from '../../utils/Utils';

const updateManager = NativeModules.AppUpdateManager;

/**
 * opk更新相关类（主要用于插件的更新及卸载）
 */
export class AppUpdateManager {
    private static readonly appId = null;

    private static updateProgress: EmitterSubscription;
    private static updateResult: EmitterSubscription;

    /**
     * 当前是否正在进行初始化安装
     * @returns
     */
    public static isInitInstall(): boolean {
        if (initialProps.isSupportPlugin) {
            return updateManager.isInitInstall(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
            return false;
        }
    }

    /**
     * 是否正在进行更新
     * @returns
     */
    public static isUpdating(): boolean {
        if (initialProps.isSupportPlugin) {
            return updateManager.isUpdating(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
            return false;
        }
    }

    /**
     * 检查是否需要更新
     * @returns
     */
    public static checkUpdate(): boolean {
        if (initialProps.isSupportPlugin) {
            return updateManager.checkUpdate(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
            return false;
        }
    }

    /**
     * 设置是否允许更新
     * @param enable
     */
    public static setUpdateEnable(enable: boolean) {
        if (initialProps.isSupportDisableUpdate) {
            updateManager.setUpdateEnable(this.appId, enable);
        } else {
            newError(DEV_ERROR_SUPPORT);
        }
    }

    /**
     * 设置更新进度监听
     * @param listener
     */
    public static setUpdateListener(
        updateStatus: (
            count: number,
            index: number,
            appName: string,
            oper: string
        ) => void,
        updateResult: (result: boolean, message: string) => void
    ) {
        if (!initialProps.isSupportPlugin) {
            newError(DEV_ERROR_SUPPORT);
            return;
        }

        if (this.updateProgress) {
            this.updateProgress.remove();
        }

        this.updateProgress = DeviceEventEmitter.addListener(
            'updateProgress',
            (data: any) => {
                let status = JSON.parse(data);
                updateStatus(
                    status.count,
                    status.index,
                    status.appName,
                    status.oper
                );
            }
        );

        if (this.updateResult) {
            this.updateResult.remove();
        }

        this.updateResult = DeviceEventEmitter.addListener(
            'updateResult',
            (data: any) => {
                let result = JSON.parse(data);
                updateResult(result.result, result.message);
            }
        );

        updateManager.setUpdateListener();
    }

    /**
     * 开始更新
     */
    public static startUpdate() {
        if (initialProps.isSupportPlugin) {
            updateManager.startUpdate(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
        }
    }

    /**
     * 更新重试
     */
    public static retryUpdate() {
        if (initialProps.isSupportPlugin) {
            updateManager.retryUpdate(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
        }
    }

    public static cancelUpdate() {
        if (initialProps.isSupportPlugin) {
            updateManager.cancelUpdate(this.appId);
        } else {
            newError(DEV_ERROR_SUPPORT);
        }
    }
}
