import { DeviceEventEmitter } from 'react-native';
import { TriggerType } from './Trigger';
import { BaseVoice, SpeakInfo } from './BaseVoice';
import { BaseComponent, BaseComponentProps } from './BaseComponent';
import { recognitionModel } from './models/RecognitionModel';
import { emojiPlayerModel } from './models/EmojiPlayerModel';
import { faceParticleModel } from './models/FaceParticleModel';

export abstract class BaseViewModel {
    protected _channel: string;
    protected _voice?: BaseVoice;
    protected _view?: BaseComponent<
        BaseComponentProps,
        BaseViewModel,
        BaseVoice
    >;
    protected status: string;
    protected recognitionStore = recognitionModel;
    protected emojiPlayerStore = emojiPlayerModel;
    protected faceParticleStore = faceParticleModel;
    private readonly appId = null;

    public constructor(channel: string) {
        if (this.appId) {
            this._channel = this.appId + '_' + channel;
        } else {
            this._channel = channel;
        }
        this.status = 'stop';
    }

    public setVoice = (voice: BaseVoice): BaseViewModel => {
        this._voice = voice;
        this._voice && this._voice.start();
        return this;
    };

    public setSpeakInfoMap = (speakInfoMap: Map<number, SpeakInfo>): void => {
        if (this._voice) {
            this._voice.setSpeakInfoMap(speakInfoMap);
        }
    };

    public updateSpeakInfo = (speakInfo: SpeakInfo): void => {
        if (this._voice) {
            this._voice.updateSpeakInfo(speakInfo);
        }
    };

    // setTrigger = (trigger: Trigger) => {
    //     this._trigger = trigger;
    //     return this;
    // };

    public setView = (
        view: BaseComponent<BaseComponentProps, BaseViewModel, BaseVoice>
    ): BaseViewModel => {
        this._view = view;
        return this;
    };

    public getChannel = (): string => {
        return this._channel;
    };

    public _trigger = (type: TriggerType, eventId: number, data: any): void => {
        // this.onStop();
        let protocol = {
            channel: this._channel,
            eventId: eventId,
            type: type,
            result: data
        };
        console.log('trigger: ', protocol);
        DeviceEventEmitter.emit('__trigger__', protocol);
    };

    public _uiTrigger = (eventId: number, data: any): void => {
        this._trigger(TriggerType.ui, eventId, data);
    };

    public _voiceTrigger = (
        eventId: number,
        data: any,
        queryType?: number
    ): void => {
        if (queryType === 2) {
            this._trigger(TriggerType.ui, eventId, data);
        } else {
            this._trigger(TriggerType.voice, eventId, data);
        }
    };

    public _apiTrigger = (eventId: number, data: any): void => {
        this._trigger(TriggerType.api, eventId, data);
    };

    public start = (): void => {
        if (this.status === 'stop') {
            this.status = 'start';
            // this._trigger && triggerManager.addTrigger(this._trigger);
            this._voice && this._voice.start();
            this.onStart();
        }
    };

    public stop = (): void => {
        if (this.status === 'start') {
            this.status = 'stop';
            // this._trigger && triggerManager.removeTrigger(this._trigger);
            this._voice && this._voice.stop();
            this.onStop();
            this._voice = undefined;
            this._view = undefined;
        }
    };

    abstract onStart(): void;
    abstract onStop(): void;

    public showEmojiPlayer = (): void => {
        this.emojiPlayerStore.setShow(true);
    };

    public hideEmojiPlayer = (): void => {
        this.emojiPlayerStore.setShow(false);
    };

    public startEmojiTalk = (): void => {
        this.emojiPlayerStore.setTalk(true);
    };

    public stopEmojiTalk = (): void => {
        this.emojiPlayerStore.setTalk(false);
    };

    public showRecognition = (): void => {
        this.recognitionStore.setShow(true);
    };

    public hideRecognition = (): void => {
        this.recognitionStore.setShow(false);
    };

    public showFaceParticle = (): void => {
        this.faceParticleStore.setHide(false);
    };

    public hideFaceParticle = (): void => {
        this.faceParticleStore.setHide(true);
    };

    public showEyeCenter = (): void => {
        this.faceParticleStore.setShowCenter(true);
    };

    public showEyeLeft = (): void => {
        this.faceParticleStore.setShowCenter(false);
    };

    public showBackgroundStar = (): void => {
        this.faceParticleStore.setShowStar(true);
    };

    public hideBackgroundStar = (): void => {
        this.faceParticleStore.setShowStar(false);
    };
}
