
/* eslint-disable @typescript-eslint/no-parameter-properties */

/**
 * 上报机器人当前任务事件以及子任务状态
 * @param taskId 事件所属任务id
 * @param taskData 任务数据
 * @param eventType 事件类型
 * @param eventData 事件数据
 * @param subTaskId 子任务id
 * @param subTaskType 子任务类型
 * @param subTaskData 子任务数据
 * @param isCreateOnStart 是否服务端主动创建任务
 */
export class TaskEvent {
    public constructor(
        public taskId: string,
        public taskData: string,
        public eventType: string,
        public eventData: string,
        public subTaskId: string,
        public subTaskType: string,
        public subTaskData: string,
        public isCreateOnStart: number
    ) {
        this.taskId = taskId;
        this.taskData = taskData;
        this.eventType = eventType;
        this.eventData = eventData;
        this.subTaskId = subTaskId;
        this.subTaskType = subTaskType;
        this.subTaskData = subTaskData;
        this.isCreateOnStart = isCreateOnStart;
    }
}